    <section class="privacy-content">

      <span class="privacy-content-title">脉迩生活隐私政策</span>

      <span>【前言】脉迩生活（指脉迩生活网站及其移动应用产品，以下统称“脉迩生活”）系上海移视网络科技有限公司（以下统称“移视”或“我们”）提供心电监测服务的产品。我们一向庄严承诺保护使用脉迩生活之用户（以下统称“用户”或“您”）的个人信息和隐私安全。您在使用脉迩生活时，我们可能会收集和使用您的相关个人信息。我们希望通过《脉迩生活隐私政策》（以下简称“本政策”）向您说明我们在收集和使用您相关个人信息时对应的处理规则等相关事宜，以便更好地保障您的权益。<b>【特别提示】在使用脉迩生活前，请您务必仔细阅读并透彻理解本政策，特别是以粗体标识的条款，您应重点阅读，在确认充分理解并同意后再开始使用。如果您不同意本政策的内容，将可能导致我们的产品和服务无法正常运行，或者无法达到我们拟达到的服务效果，您应立即停止访问/使用我们的产品与/或服务。</b></span>
      <span>本政策适用于脉迩生活，如与《脉迩生活用户协议》同类条款之约定不一致的，以本政策为准。在阅读完本政策后，如您对本政策或与本政策相关的事宜有任何问题，您可通过本政策“如何联系我们”章节所列的反馈渠道联系我们，我们会尽快为您作出解答。</span>
      <span><b>本政策将帮助您了解以下内容：</b></span>
      <span><b>一、我们如何收集和使用个人信息</b></span>
      <span><b>二、我们可能共享的个人信息</b></span>
      <span><b>三、我们如何存储和保护个人信息</b></span>
      <span><b>四、如何管理您的个人信息</b></span>
      <span><b>五、未成年人保护</b></span>
      <span><b>六、通知和修订</b></span>
      <span><b>七、如何联系我们</b></span>

      <span><b>一、我们如何收集和使用个人信息</b></span>
      <span>我们收集您的个人信息主要是为了您和其他用户能够更容易和更满意地使用脉迩生活。我们的目标是向所有的互联网用户提供安全、有用及有益的使用经历，而这些信息有助于我们实现这一目标。</span>
      <span><b>（一）我们将通过以下途径收集和获得您的个人信息：</b></span>
      <span><b>1、您提供的信息。</b>包括：</span>
      <span>（1）您在注册脉迩生活的账号或使用脉迩生活时，向我们提供的信息；</span>
      <span>（2）您通过脉迩生活向第三方提供的共享信息，以及您使用脉迩生活时所存储的信息。</span>
      <span><b>请注意，如您在脉迩生活内上传或发布的信息、当您发现他人不正当地收集或使用您的信息时，可通过本政策“如何联系我们”章节所列的反馈渠道联系我们。</b></span>
      <span><b>2、我们获取的您的信息。</b>您在使用脉迩生活时，我们收集、汇总、记录的信息，包括日志信息、位置信息、设备信息，蓝牙信息等。</span>
      <span><b>（二）我们会出于以下目的，收集和使用您以下类型的个人信息：</b></span>
      <span>1、当您注册或登录脉迩生活服务时，为便于我们为您提供持续稳定的服务，并保障您使用脉迩生活的安全性，我们需要您提供基本注册或登录信息，<b>包括手机号码、验证码匹配结果、用户名和密码。</b></span>
      <span>2、当您使用脉迩生活服务时，您需要选择填写姓名，性别，否则会影响脉迩生活为您提供AI报告和医生报告等基本功能。您可以选择补充您的额外信息（包括您的身高、体重、年龄等信息），这将有助于我们给您提供更优的服务体验，但如果您不提供这些信息，并不会影响您使用脉迩生活的基本功能。</span>
      <span>3、当您使用脉迩生活服务时，为保障您正常使用我们的服务，维护我们服务的正常运行，改进及优化我们的服务体验以及保障您的帐号安全，我们会收集、使用您的设备型号、操作系统、唯一设备标识符（Android端如AndroidID/OAID, iOS端如iOS14.5之前的IDFA）、登录IP地址、脉迩生活软件版本号、接入网络的方式、类型和状态、网络质量数据、设备加速器（如重力感应设备）、操作日志、服务日志信息（如您在脉迩生活进行搜索、查看操作的记录、服务故障信息、引荐网址等信息）等日志信息。同时，为了预防病毒、木马程序或其他恶意程序、网站，我们可能会收集您设备安装的应用信息、正在运行的进程信息或设备内存中寄存的数据，以防止您的个人信息泄露。</span>
      <span>4、当您使用脉迩生活报告功能时，我们将使用您的心电数据、蓝牙数据、接入网络的方式、类型和状态、网络质量数据，设备型号，唯一设备标识符等信息。</span>
      <span>5、当您使用分享功能时，我们将使用您手机app调取权限，将您的分析报告分享给微信好友。</span>
      <span>6、当您使用消息推送功能时，我们会在获得您的同意后，收集您的位置信息。拒绝提供该信息可能会使您延迟获取推送的信息，或无法达到上述基于位置信息提供的相关服务拟达到的效果，但不影响您正常使用脉迩生活的其他功能。集成的极光推送(cn.jiguang)自启动服务用于提高消息推送通达度。为方便用户在后台杀死的情况下可以提供推送服务。</span>
      <span>7、当您使用脉迩生活心律失常监测、心脏负荷监测、心肌缺血监测血压监测等功能时，我们收集的信息包括用户的心电数据，蓝牙数据，接入网络的方式、类型和状态、网络质量数据，设备型号，唯一设备标识符，这是实现这一功能所必需的。</span>
      <span>8、当您联系我们的客服或使用其他用户响应功能时（例如提出注销申请、个人信息保护投诉或建议、其他客户投诉和需求），<b>我们可能需要您提供必要的个人信息以匹配并核验您的用户身份，</b>以便保障您的账号与系统安全。<b>我们可能还会保存您的联系方式（您与我们联系时使用的或您向我们主动提供的其他联系方式）、您与我们的通信/通话记录和内容、与您需求相关联的其他必要信息，</b>以便与您联系或帮助您解决问题，或记录相关问题的处理方案及结果。</span>
      <span>9、为向您提供您所选择使用的服务或者保证服务的质量和体验，脉迩生活可能需要您授权开启设备的系统权限。如您需要关闭您为使用上述某些特定服务而开启的功能权限，大多数设备都会支持您的这项需求，具体方法请参考或联系您移动设备的服务商或生产商。请您注意，您开启任一权限即代表您授权我们可以收集和使用相关信息来为您提供对应服务，您一旦关闭任一权限即代表您取消了授权，我们将不再基于对应权限继续收集和使用相关信息，也无法为您提供该权限所对应的服务。但是，您关闭权限的决定不会影响此前基于您的授权所进行的信息收集及使用。</span>
      <span>10、当你使用脉迩生活我的个人信息上传头像功能时，我们会获取您的相机权限这是实现该功能所必需的；</span>
      <span>11、查看报告功能和下载app功能，将获取您的存储权限；</span>
      <span><b>12、根据相关法律法规及国家标准，以下情形中，我们可能会处理您的相关个人信息而无需征求您的授权同意：</b></span>
      <span><b>（1）履行法律法规规定的义务相关的；</b></span>
      <span><b>（2）与国家安全、国防安全直接相关的；</b></span>
      <span><b>（3）与公共安全、公共卫生、重大公共利益直接相关的；</b></span>
      <span><b>（4）与刑事侦查、起诉、审判和判决执行等有关的；</b></span>
      <span><b>（5）出于维护您或其他个人的生命、财产等重大合法权益但又很难得到您本人同意的；</b></span>
      <span><b>（6）所收集的信息是您自行向社会公众公开的；</b></span>
      <span><b>（7）从合法公开披露的信息中收集信息的，如合法的新闻报道、政府信息公开等渠道；</b></span>
      <span><b>（8）维护脉迩生活的安全稳定运行所必需的，例如发现、处置产品或服务的故障；</b></span>
      <span><b>（9）开展合法的新闻报道所必需的；</b></span>
      <span><b>（10）出于公共利益开展统计或学术研究所必要，且对外提供学术研究或描述的结果时，对结果中所包含的信息进行去标识化处理的；</b></span>
      <span><b>（11）法律法规规定的其他情形。</b></span>
      <span><b>13、敏感个人信息，是指一旦泄露、非法提供或滥用可能危害人身和财产安全，极易导致个人名誉、身心健康受到损害或歧视性待遇等的个人信息。以上由您提供或我们收集您的信息中，可能包含您的敏感个人信息，行踪轨迹、位置信息。请您谨慎并留意敏感个人信息，您同意我们可以按本政策所述的目的和方式来处理您的敏感个人信息。</b></span>

      <span><b>二、我们可能共享的个人信息</b></span>
      <span>我们非常重视保护您的个人信息。除以下情形外，我们不会与我们之外的任何第三方共享您的个人信息：</span>
      <span><b>1、为实现我们的服务/功能所必需进行的共享。</b>在某些情况下，我们需要向第三方共享您的信息，以实现我们产品与/或服务的功能，让您正常使用您需要的服务，包括：（1）向提供消息推送服务的第三方机构共享对应的<b>用户ID和定位系统；</b>（2）向实名认证服务商共享<b>实名认证所需资料；</b>（3）其他与我们的服务/功能相关的供应商、服务提供商和业务合作伙伴所必需进行的共享。</span>
      <span><b>2、与第三方SDK或其他类似应用程序的共享。</b>为保障脉迩生活的稳定运行、功能实现，我们可能会接入第三方服务商提供的SDK或其他类似应用程序（如以嵌入代码、插件等形式），以便向您提供更好的客户服务和用户体验。我们接入的部分第三方SDK或其他类似应用程序可能涉及收集您的个人信息，用于向您提供相应的服务。我们会评估该第三方收集信息的合法性、正当性、必要性，要求第三方对您的信息采取保护措施，并严格遵守相关法律法规与监管要求。</span>
      <span><b>您可以通过查看下方《第三方信息共享清单》详细了解脉迩生活接入的第三方SDK或其他第三方合作商的名称、用途、收集的个人信息类型等信息。</b></span>
