      <span><b>三、我们如何存储和保护个人信息</b></span>
      <span><b>（一）个人信息的存储</b></span>
      <span><b>除法律法规或监管部门另有规定外，我们仅在本政策所述目的所必需且最短时限内存储您的个人信息。</b>如我们终止服务或运营，我们将及时停止继续收集您个人信息的活动，同时会遵守相关法律法规要求提前向您通知，并在终止服务或运营后对您的个人信息进行删除或匿名化处理，但法律法规或监管部门另有规定的除外。</span>
      <span><b>我们在中华人民共和国境内运营中收集和产生的个人信息，存储在中国境内。但以下情形除外：</b></span>
      <span><b>1、法律法规有明确规定；</b></span>
      <span><b>2、获得您的授权同意；</b></span>
      <span><b>3、您使用的产品、服务涉及跨境，我们需要向境外提供您的个人信息的。</b></span>
      <span>针对以上情形，我们会确保依据国家法律法规和相关监管部门的规定执行，并对您的个人信息提供足够的保护。</span>
      <span><b>（二）个人信息的保护</b></span>
      <span>我们非常重视信息安全，成立了专门的安全团队，并采取一切合理可行的措施，保护您的个人信息：</span>
      <span><b>1、我们仅允许有必要知晓这些信息的移视员工、合作伙伴访问您的个人信息，并为此设置了严格的访问权限控制和监控机制。我们同时要求可能接触到您的个人信息的所有人员履行相应的保密义务。如果未能履行这些义务，可能会被追究法律责任或被中止与脉迩生活的合作关系。</b></span>
      <span>2、互联网并非绝对安全的环境，而且电子邮件、即时通讯、社交软件或其他服务软件等与其他用户的交流方式无法确定是否完全加密，我们建议您使用此类工具时请使用复杂密码，并注意保护您的信息安全。在通过脉迩生活与第三方进行沟通或购买商品及服务时，您不可避免的要向交易对方或潜在的交易对方披露自己的信息，如联络方式或者邮政地址等。请您妥善保护自己的信息，仅在必要的情形下向他人提供。</span>
      <span>3、互联网环境并非百分之百安全，我们将尽力确保或担保您发送给我们的个人信息的安全性。如果我们的物理、技术、或管理防护设施遭到破坏，导致个人信息被非授权访问、公开披露、篡改、或毁坏，导致您的合法权益受损，我们将承担相应的法律责任。</span>
      <span><b>4、安全事件处置</b></span>
      <span>为应对个人信息泄露、损毁和丢失等可能出现的风险，我们制定了多项制度，明确安全事件、安全漏洞的分类分级标准及相应的处理流程。我们也为安全事件建立了专门的应急响应团队，按照安全事件处置规范要求，针对不同安全事件启动安全预案，进行止损、分析、定位、制定补救措施、联合相关部门进行溯源和打击。我们亦定期组织内部培训和应急演练，使得我们的相关工作人员掌握相应的应急处置策略和规程。</span>
      <span>在不幸发生信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们同时将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，主动上报信息安全事件的处置情况。</span>
      <span><b>请您理解，由于技术的限制以及风险防范的局限，即便我们已经尽力加强安全措施，也无法始终保证信息百分之百的安全。您需要了解，您接入脉迩生活所用的系统和通讯网络，有可能因我们可控范围外的情况而发生问题。</b></span>
      <span>请您务必妥善保管好您的账号、密码及其他身份要素。您在使用脉迩生活时，我们会通过您的账号、密码及及其他身份要素来识别您的身份。一旦您泄露了前述信息，您可能会蒙受损失，并可能对您产生不利。如您发现账号、密码及/或其他身份要素可能或已经泄露时，请您立即和我们取得联系，以便我们及时采取相应措施以避免或降低相关损失。</span>

      <span><b>四、如何管理您的个人信息</b></span>
      <span><b>（一）访问、更新和删除</b></span>
      <span>我们鼓励您更新和修改您的个人信息以使其更准确有效。</span>
      <span>您可以通过【我的】-点击您的头像-【修改个人信息】，查询和修改您的头像、昵称、出生日期，疾病史、实名认证等。</span>
      <span>在以下情形中，您可以直接向我们提出删除个人信息的请求：</span>
      <span>1、我们收集、使用您的个人信息的行为违反相关法律法规；</span>
      <span>2、我们收集、使用您的个人信息未征得您的同意；</span>
      <span>3、我们收集、使用您的个人信息的行为违反了与您的约定；</span>
      <span>4、法律法规规定的其他情形。</span>
      <span>您也可以通过本政策“如何联系我们”章节所列的反馈渠道联系我们为您处理。我们将采取适当的技术手段或联系渠道，尽可能保证您可以访问、更新和删除自己的个人信息或使用脉迩生活时提供的其他信息。 除法律法规另有规定，当您更正、删除您的个人信息后，我们可能不会立即从备份系统中删除相应的信息，但会在备份更新时更正或删除这些信息。</span>


      <span><b>（二）公开与分享</b></span>
      <span>脉迩生活可以将您的信息通过您的主动操作分享给微信好友或微信朋友圈，例如，您在脉迩生活中产生的报告、只要您不删除您所分享的报告信息，数据内容会一直存在且可以反复查看。<b>如您将信息通过上述渠道公开或分享，由此造成您的信息泄露，我们不承担责任。因此我们提醒并请您慎重考虑是否通过上述渠道公开或分享您的信息。</b></span>
      <span><b>（三）改变您授权同意的范围</b></span>
      <span><b>您总是可以选择是否向我们披露个人信息。有些个人信息是使用脉迩生活所必需的，但大多数其他信息的提供是由您决定的。您可以通过删除信息、关闭设备功能等方式改变您授权我们继续收集信息的范围或撤回您的授权。</b></span>
      <span><b>当撤回授权后，我们无法继续为您提供撤回授权所对应的服务，也不再处理您相应的信息。但您撤回授权的决定，不会影响此前基于您的授权而开展的信息处理。</b></span>
      <span><b>（四）约束信息系统自动决策</b></span>
      <span><b>在某些业务功能中，我们可能仅依据信息系统、算法等在内的非人工自动决策机制作出决定。如果这些决定显著影响您的合法权益，您有权要求我们作出解释，我们也将提供适当的救济方式。</b></span>
      <span><b>（五）响应您的上述请求</b></span>
      <span><b>为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情收取一定成本费用。对于那些无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际（例如，涉及备份磁带上存放的信息）的请求，我们可能会予以拒绝。</b></span>
      <span><b>在以下情形中，我们将无法响应您的请求：</b></span>
      <span><b>1、与我们履行法律法规规定的义务相关的；</b></span>
      <span><b>2、与国家安全、国防安全直接相关的；</b></span>
      <span><b>3、与公共安全、公共卫生、重大公共利益直接相关的；</b></span>
      <span><b>4、与刑事侦查、起诉、审判和执行判决等直接相关的；</b></span>
      <span><b>5、我们有充分证据表明您存在主观恶意或滥用权利的；</b></span>
      <span><b>6、出于维护您或其他个人的生命、财产等重大合法权益但又很难得到您本人同意的；</b></span>
      <span><b>7、响应您的请求将导致您或其他个人、组织的合法权益受到严重损害的；</b></span>
      <span><b>8、涉及商业秘密的。</b></span>

      <span><b>五、未成年人保护</b></span>
      <span><b>任何18周岁以下的未成年人均不得创建自己的用户账户及/或使用我们的服务。</b></span>
      <span><b>我们只会在法律法规允许、父母或监护人明确同意或者保护未成年人所必要的情况下使用或披露未成年人的个人信息。</b></span>

      <span><b>六、修订和通知</b></span>
      <span><b>为了给您提供更好的服务，我们可能会根据产品或服务的更新情况及法律法规的相关要求适时修改本政策的条款，该等修改构成本政策的一部分。我们会在本页面上公布对本政策所作的任何变更。对于重大变更，我们还会提供更为显著的通知（包括网站公告、推送通知、弹窗提示或其他方式），本政策所指的重大变更包括但不限于：</b></span>
      <span>1、我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；</span>
      <span>2、我们在所有权结构方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；</span>
      <span>3、个人信息共享、转让或公开披露的主要对象发生重大变化；</span>
      <span>4、您参与个人信息处理方面的权利及其行使方式发生重大变化；</span>
      <span>5、我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生重大变化；</span>
      <span>6、其他重要的或可能严重影响您的个人权益的情况发生。</span>
      <span><b>您如果不同意该等变更，可以选择停止使用脉迩生活；如您仍然继续使用脉迩生活，即表示您已充分阅读、理解并同意受经修订的本政策的约束。</b></span>
      <span>我们的任何修改都会将您的满意度置于首位。我们鼓励您在每次使用脉迩生活时都查阅我们的隐私政策。</span>

      <span><b>七、如何联系我们</b></span>
      <span><b>当您有投诉、建议、举报或注销账号等相关问题时，请通过以下方式与我们联系：</b></span>
      <span><b>邮箱：</b>yishi@mvtech.cc。</span>
      <span><b>我们将在收到相关反馈后，15个工作日内予以答复。</b></span>


